.container-3 {
  background-color: white;
  height: 400px;
  padding: 50px;
}

.container-4 {
  background-color: #f1e4e8;
  height: 300px;
  padding: 50px;
}

.container-5 {
  background-color: #2d2d34;
  height: 100px;
  padding: 50px;
}

.h2 {
  padding: 200px;
}

.footer {
  display: flex;
  flex-direction: column;
  align-content: center;
  background-color: #2d2d34;
  height: 20vh;
  text-justify: center;
  color: lightgray;
  justify-content: space-evenly;
}
.footer a {
  color: #d2d2d2;
  text-decoration: none;
}
.footer p {
  font-family: sans-serif;
  font-weight: small;
}

.footert {
  display: flex;
  flex-direction: row;
  text-align: center;
  justify-content: center;
  align-items: center;
  justify-content: space-between;
  margin-left: 42%;
  margin-right: 42%;
}

.footersocial {
  display: flex;
  flex-direction: row;
  text-align: center;
  justify-content: center;
  align-items: center;
  justify-content: space-between;
  margin-left: 45%;
  margin-right: 45%;
}

.footersocial a {
  color: #d2d2d2;
}

.contact {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-content: center;
  background-color: #c6afb1;
  /* silver pink: */
  /* background-color: #c6afb1; */
  /* "old lavender*/
  /* background-color: #685f74; */
  font-size: large;
  height: 70vh;
  flex-wrap: wrap;
  font-family: sans-serif;
}
.content {
  justify-content: center;
  font-size: normal;
  text-align: center;
  font-family: sans-serif;
  font-weight: lighter;
  line-height: 1.8;
}
.content a {
  color: #5a2337;
  text-decoration: none;
}
.contact h2 {
  /* font-family: sans-serif; */
  font-family: "Times New Roman", Times, serif;
  font-weight: normal;
  font-size: xx-large;
}
.CVbutton {
  border: none;
  color: white;
  padding: 16px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  transition-duration: 0.4s;
  cursor: pointer;
  background-color: #c6afb1;
  color: #2d2d34;
  border: 4px solid #2d2d34;
}

.CVbutton:hover {
  border: 4px solid white;
  color: white;
}

.projectMain {
  background-color: #e4deda;
  text-align: center;
  padding: 4%;
  height: auto;
}

.flexProjects {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  height: auto;
}

h3 {
  font-family: sans-serif;
  font-weight: normal;
  font-size: x-large;
}

.projectContent p {
  font-family: sans-serif;
  font-weight: lighter;
  font-size: medium;
}
.projectContent h2 {
  font-family: sans-serif;
  font-weight: normal;
  font-size: x-large;
}

.projectMain h2 {
  /* font-family: sans-serif; */
  font-family: "Times New Roman", Times, serif;
  font-weight: normal;
  font-size: xx-large;
}

.example {
  display: flex;
  flex-flow: row wrap;
  height: auto;
  width: 40%;
  margin: 2%;
  justify-content: center;
  text-align: center;
  position: relative;
  font-size: 30px;
}

.projectImage {
  width: 100%;
  height: auto;
}

.projectContent {
  align-items: center;
  justify-content: center;
  /*object-fit: cover;*/
  font-size: x-large;
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  font-family: sans-serif;
  font-weight: lighter;
  background-color: rgba(200, 200, 200, 0.5);
  width: 100%;
  height: 100%;
  transition: all 400ms ease-out;
  text-align: center;
}

.example:hover .projectContent {
  opacity: 1;
}

.firstpage {
  display: flex;
  flex-direction: row;
  flex-flow: row wrap;
  justify-content: space-evenly;
  align-content: center;
  background-color: #f5f2f0;
  font-size: large;
  flex-wrap: wrap;
  height: 100vh;
}

.meInfo {
  display: flex;
  flex-direction: column;
  width: 500px;
  text-align: left;
  margin-top: 8vh;
}

.meInfo .firstname {
  font-family: "Times New Roman", Times, serif;
  font-size: 6vw;
  color: rgb(42, 42, 42);
  font-weight: 500;
  text-align: left;
  margin: 0;
}
.meInfo .lastname {
  font-family: "Times New Roman", Times, serif;
  font-size: 6vw;
  color: rgb(42, 42, 42);
  font-weight: 500;
  text-align: left;
  margin: 0 0 0 60px;
}

.meImage {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 5%;
}

.me {
  width: 50vh;
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
}

.meInfo p {
  font-size: small;
  font-family: sans-serif;
  color: rgb(42, 42, 42);
  line-height: 1.8;
  margin: 20px 0 0 130px;
  font-weight: lighter;
}

.arrow-down {
  border-right: 5px solid rgb(100, 100, 100);
  border-top: 5px solid rgb(100, 100, 100);
  height: 1rem;
  position: absolute;
  bottom: 5vh;
  transform: rotate(135deg);
  width: 1rem;
}

.about {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-content: center;
  background-color: #f7f5f3;
  font-size: large;
  height: auto;
  flex-wrap: wrap;
  text-align: center;
  padding: 4%;
}

.aboutContent {
  justify-content: center;
  font-size: normal;
  text-align: left;
  width: 60%;
}

.about h2 {
  /* font-family: sans-serif; */
  font-family: "Times New Roman", Times, serif;
  font-weight: normal;
  font-size: xx-large;
}

.aboutContent p {
  font-size: normal;
  font-family: sans-serif;
  color: rgb(42, 42, 42);
  line-height: 1.8;
  font-weight: lighter;
}

